
.background{
  background-image: url('backgroung.jpg');
  width: 100vw;
  height: 100vh;
  background-repeat: no-repeat;
  background-size: cover;
} 

.container{
  background-image: url('back.webp');
  height: 60vh;
  width: 30%;
  margin-left: 30%;
  text-align: center;
  margin-top: 6%;
  border-radius: 20px;
  position: absolute;
}

.display input{
  width: 90%;
  background-color: cornflowerblue;
  font-size: 30px;
  text-align: right;
  margin: 30px 0px;
  border-radius: 10px;
} 
form div input{
  background-color: rgb(237, 219, 227);
  width: 18%;
  text-align: center;
  margin:3px 3px 4px 4px ;
  font-size: 35px;
  border-radius: 10px;
}

form div input:hover{
  cursor: pointer;
  background-color: gray;
}

@media only screen and (max-width: 768px) {
  .container{
      width: 90%;
      margin: 10px 15px;
  }
}
@media only screen and (min-width: 768px) {
  .container{
      width: 60%;
       margin: 8% 15%;
       height: 80%;
  }
  .display input{
    width: 80%;
  }
  form div input{
     
      height: auto;
  }
}